import _ from '@jmaguirrei/belt';export default client => {  const { Input } = client.ui.components;  return client.hoc({    state(props, store) {      return {        language: store.get('language'),        email: store.get('signin.email'),      };    },    actions(props, store) {      return {        onInput: e => {          const email = e.target.value;          store.set({ 'signin.email': email, 'forgot.email': email });        },        onClickRight: () => {          const { state, actions } = client.lib.SigninControl(store);          const enabled = state.rightEnabled;          if (enabled) actions.onClickRight();        },      };    },    styles: {      wrapper: `        display: flex;        flex-flow: column;        height: 100%;        justify-content: space-around;      `,      title: `        font-size: 28px;        padding: 20px 20px 10px 20px;      `,      subtitle: `        font-size: 24px;        padding: 10px 20px 20px 20px;      `,    },    render({ state, styles, actions }) {      const { language, email } = state;      return client.html`        <div style=${styles.wrapper}>          <div>            <div style=${styles.title}>              ${_.get({                en: 'Welcome back!',                es: '¡Bienvenid@ de nuevo!',              }, language)}            </div>            <div style=${styles.subtitle}>              ${_.get({                en: 'Enter your email',                es: 'Ingresa tu email',              }, language)}            </div>          </div>          ${Input({            label: 'email',            type: 'text',            placeholder: _.get({ en: 'Your email', es: 'Tu email' }, language),            value: email,            onInput: actions.onInput,            onClickRight: actions.onClickRight,          })}        </div>      `;    }  });};