// import _ from '@jmaguirrei/belt';export default Store => () => {  const email = Store.observables['signin.email'];  const password = Store.observables['signin.password'];  return {    steps: [      () => ({        domain: '_Backend_',        method: 'signinValidatePassword',        optimistic: false,        args: {          email,          password,        },      }),      res => ({        domain: '_Store_',        method: res.error ? 'alertOn' : 'set',        args: res.error ? {          name: res.error,          timeout: 4000,        } : null,        sideEffect: () => {          if (!res.error) {            const { user_id, isPasswordOK } = res.data;            if (isPasswordOK) {              window.location.replace(`https://museeker.io/app?user=${user_id}`);            }          }        },      }),    ],  };};