// import _ from '@jmaguirrei/belt';export default Store => () => {  const email = Store.observables['signin.email'].toLowerCase();  return {    steps: [      () => ({        domain: '_Backend_',        method: 'signinValidateEmail',        optimistic: false,        args: {          email,        },      }),      res => ({        domain: '_Store_',        method: res.error ? 'alertOn' : 'set',        args: res.error ? {          name: res.error,          timeout: 4000,        } : {          'signin.name': res.data.name,          'signin.currentStep': 1,        },      }),      res => ({        domain: '_Store_',        method: 'set',        args: res.error ? {          'signup.email': email,        } : null,      }),    ],  };};