export default client => {  const { Carrousel, Navigation } = client.ui.components;  const { Header, Tabs } = client.ui.fragments;  return client.hoc({    state(props, store) {      return client.lib.ForgotControl(store).state;    },    actions(props, store) {      return client.lib.ForgotControl(store).actions;    },    classes: {      container: `        display: flex;        flex-flow: column;        justify-content: space-around;        height: 100%;        position: relative;      `,    },    sections: {      Email: require('./<>/Email').default,      Password: require('./<>/Password').default,      Token: require('./<>/Token').default,    },    render({ state, actions, classes, sections }) {      const screens = Object.keys(sections).map(key => sections[key]);      const numSteps = screens.length;      const { currentStep, leftEnabled, rightEnabled, checkText, alertText } = state;      const { onClickLeft, onClickRight } = actions;      return client.html`        <div id='forgot' class=${classes.container}>          ${Header({ numSteps, currentStep })}          ${Carrousel({ currentStep, screens })}          ${Navigation({            currentPage: 'forgot',            onClickLeft,            onClickRight,            leftEnabled,            rightEnabled,            text: alertText || checkText,            color: alertText ? client.lib.Colors.RED_WARNING : '',          })}          ${Tabs()}        </div>      `;    }  });};