export default Store => () => {  const _id = Store.observables['user_id'];  const password = Store.observables['signup.password'];  const tokenDigits = Store.observables['signup.tokenDigits'];  return {    steps: [      () => ({        domain: '_Backend_',        method: 'signupFinishRegistration',        optimistic: false,        args: {          _id,          password,          token: tokenDigits.join(''),        },      }),      res => ({        domain: '_Store_',        method: res.error ? 'alertOn' : 'set',        args: res.error ? {          name: res.error,          timeout: 4000,        } : {          'signup.currentStep': 0,          'signup.name': '',          'signup.email': '',          'signup.password': '',          'signup.tokenDigits': [],          currentPage: 'welcome',        },        sideEffect: () => {          if (!res.error) {            window.history.replaceState(null, null, '/welcome');          }        }      }),      res => ({        domain: '_Store_',        method: 'set',        args: res.error ? {          'signup.tokenDigits': [],        } : null,      }),    ],  };};