import _ from '@jmaguirrei/belt';export default client => {  const { Input } = client.ui.components;  return client.hoc({    state(props, store) {      return {        language: store.get('language'),        name: store.get('signup.name'),      };    },    actions(props, store) {      return {        onInput: e => {          store.set({ 'signup.name': e.target.value });        },        onClickRight: () => {          const { state, actions } = client.lib.SignupControl(store);          const enabled = state.rightEnabled;          if (enabled) actions.onClickRight();        },      };    },    styles: {      wrapper: `        display: flex;        flex-flow: column;        height: 100%;        justify-content: space-around;      `,      title: `        font-size: 28px;        padding: 20px 20px 10px 20px;      `,      subtitle: `        font-size: 24px;        padding: 10px 20px 20px 20px;      `,    },    render({ state, styles, actions }) {      const { language, name } = state;      return client.html`        <div style=${styles.wrapper}>          <div>            <div style=${styles.title}>              ${_.get({                en: 'Welcome!',                es: '¡Bienvenid@!',              }, language)}            </div>            <div style=${styles.subtitle}>              ${_.get({                en: 'What\'s your name?',                es: '¿Cuál es tu nombre?',              }, language)}            </div>          </div>          ${Input({            label: 'name',            type: 'text',            placeholder: _.get({ en: 'Your name', es: 'Tu nombre' }, language),            value: name,            onInput: actions.onInput,            onClickRight: actions.onClickRight,          })}        </div>      `;    }  });};