/* ------------------------------------------------------------------------------------------------===== SIGNIN WORFLOW =====0: EMAIL    isRegistered ?    - false: error    - true: continue1: PASSWORD    - ko: error    - ok: => Dashboard!    - forgot => ===== FORGOT WORFLOW =====-------------------------------------------------------------------------------------------------*/import _ from '@jmaguirrei/belt';export const SigninControl = store => {  const language = store.get('language');  const currentStep = store.get('signin.currentStep');  const currentCheck = _.get({    0: store.check('email', store.get('signin.email')),    1: store.check('password', store.get('signin.password')),  }, currentStep, {});  const checkResult = _.get(currentCheck, 'result', false);  const alert = store.alerts.find(item => item.isVisible);  return {    state: {      currentStep,      leftEnabled: currentStep > 0,      rightEnabled: checkResult,      checkText: _.get(currentCheck, `message.${language}`, ''),      alertText: _.get(alert, `message.${language}`, ''),    },    actions: {      onClickLeft: () => {        // const currentStep = store.get('signin.currentStep');        if (currentStep > 0) store.set({ 'signin.currentStep': currentStep - 1 });      },      onClickRight: () => {        // const currentStep = store.get('signin.currentStep');        if (currentStep === 0) store.call('signinEmail');        if (currentStep === 1) store.call('signinPassword');      },    },  };};