const MIN_LENGTH = 8;export const password = str => {  const noSpaces = /^\S+$/.test(str);  if (!noSpaces && str.length > 0) {    return {      result: false,      message: {        en: 'Password can\'t contain white spaces',        es: 'La contraseña no puede contener espacios',      },    };  }  const result = str.length >= MIN_LENGTH;  return {    result,    message: result ? null : {      en: `At least ${MIN_LENGTH} characters`,      es: `Mínimo ${MIN_LENGTH} caracteres`,    },  };};