export default Store => () => {  const _id = Store.observables['user_id'];  const name = Store.observables['forgot.name'];  const email = Store.observables['forgot.email'];  const password = Store.observables['forgot.password'];  const language = Store.observables.language;  return {    steps: [      /*        We go next step before setting password because there is no server validation        and email send takes time      */      () => ({        domain: '_Store_',        method: 'set',        args: {          'forgot.currentStep': 2,        },      }),      () => ({        domain: '_Backend_',        method: 'forgotSendTokenEmail',        optimistic: false,        args: {          _id,          name: name.split(' ')[0],          language,          email,          password,        },      }),    ],  };};