export default client => {  const { Modal } = client.ui.components;  return client.hoc({    state(props, store) {      return {        currentPage: store.get('currentPage') || props.currentPage,      };    },    sections: {      SignIn: require('./pages/<SignIn>/SignIn.js').default,      SignUp: require('./pages/<SignUp>/SignUp.js').default,      Welcome: require('./pages/<Welcome>/Welcome.js').default,      Forgot: require('./pages/<Forgot>/Forgot.js').default,    },    render({ state, sections }) {      const { currentPage } = state;      return Modal({        currentPage,        pages: {          signin: sections.SignIn,          signup: sections.SignUp,          welcome: sections.Welcome,          forgot: sections.Forgot,        }      });    }  });};