export default Store => () => {  const email = Store.observables['signin.email'];  const password = Store.observables['forgot.password'];  const tokenDigits = Store.observables['forgot.tokenDigits'];  return {    steps: [      () => ({        domain: '_Backend_',        method: 'forgotSetNewPassword',        optimistic: false,        args: {          email,          password,          token: tokenDigits.join(''),        },      }),      res => ({        domain: '_Store_',        method: res.error ? 'alertOn' : 'set',        args: res.error ? {          name: res.error,          timeout: 4000,        } : {          'forgot.password': '',          'forgot.tokenDigits': [],        },        sideEffect: () => {          if (!res.error) {            const { user_id } = res.data;            window.location.replace(`https://app.museeker.io?user=${user_id}`);          }        }      }),      res => ({        domain: '_Store_',        method: 'set',        args: res.error ? {          'forgot.tokenDigits': [],        } : null,      }),    ],  };};