import _ from '@jmaguirrei/belt';export default client => {  const { Input } = client.ui.components;  const { PasswordOptions } = client.ui.fragments;  return client.hoc({    state(props, store) {      return {        language: store.get('language'),        password: store.get('signup.password'),        isPasswordVisible: store.get('signup.password.isVisible'),      };    },    actions(props, store) {      return {        onInput: e => {          store.set({ 'signup.password': e.target.value });        },        onClickRight: () => {          const { state, actions } = client.lib.SignupControl(store);          const enabled = state.rightEnabled;          if (enabled) actions.onClickRight();        },      };    },    styles: {      wrapper: `        display: flex;        flex-flow: column;        height: 100%;        justify-content: space-around;      `,      title: `        font-size: 28px;        padding: 20px 20px 10px 20px;      `,      subtitle: `        font-size: 24px;        padding: 10px 20px 20px 20px;      `,      'input-wrapper': `        display: flex;        flex-flow: column;      `,    },    render({ state, styles, actions }) {      const { language, isPasswordVisible, password } = state;      return client.html`        <div style=${styles.wrapper}>          <div>            <div style=${styles.title}>              ${_.get({                en: 'Perfect, now:',                es: 'Perfecto, ahora:',              }, language)}            </div>            <div style=${styles.subtitle}>              ${_.get({                en: 'Choose a password',                es: 'Elige una contraseña',              }, language)}            </div>          </div>          <div style=${styles['input-wrapper']}>            ${Input({              label: 'password',              type: isPasswordVisible ? 'text' : 'password',              placeholder: '********',              value: password,              onInput: actions.onInput,              onClickRight: actions.onClickRight,            })}            ${PasswordOptions({ page: 'signup' })}          </div>        </div>      `;    }  });};